import React, { useEffect, useState } from "react";
import "./Nav.css";
import { Link } from "react-router-dom";
// import { useCookies } from "react-cookie";
import Img1 from "../../img/trs logo_gold.jpg";
import axios from "axios";

const Nav = () => {
  const [visible, setVisible] = useState(false);
  // const [cookies] = useCookies(["cookie-name"]);
  const [userName, setUserName] = useState('')
  const [userRole, setUserRole] = useState('')

  useEffect(()=>{
    axios.get('/admin/authCheck',{withCredentials:true})
         .then(async res=>{
            console.log(res.data.userInfo)
            if(await res.data.userInfo){
              await localStorage.setItem("userName",res.data.userInfo.name)
              await localStorage.setItem("userRole",res.data.userInfo.role)
            }else{
              await localStorage.removeItem("userName")
              await localStorage.removeItem("userRole")
            }
         })
         .catch(err=>{
            console.log(err)
         })
  },[])

  setInterval(()=>{
    // console.log(localStorage.getItem("userName"))
    setUserName(localStorage.getItem("userName"))
    setUserRole(localStorage.getItem("userRole"))
  },3000)

  // console.log(localStorage.getItem("userName"));
  return (
    <div className="Navbar">
      <div className="Navlogo">
        <img src={Img1} alt="logo"></img>
      </div>
      <div id={visible ? "hidden" : ""} className="Navdetail">
        <div className="Item">
          <Link to="/">
            <p>Home</p>
          </Link>
        </div>
        <div className="Item">
          <Link to="/About">
            <p>About</p>
          </Link>
        </div>
        {/* <div className="Item"><a href="#service"><p>Services</p></a></div> */}
        <div className="Item">
          <Link to="/Team">
            <p>Team</p>
          </Link>
        </div>
        {/* <div className="Item"><a href="#footer"><p>Contact</p></a></div> */}
        {userRole === "dealer" ? (
          <div className="Item">
            <Link to="/Data_entry">
              <p>Data</p>
            </Link>
          </div>
        ) : null}
        <div
          hidden={userName !== null}
          className="Item"
        >
          <Link to="/Signup">
            <p>Sign Up</p>
          </Link>
        </div>
        {userName === null ? (
          <div className="Item">
            <Link to="/Login">
              <p>Log In</p>
            </Link>
          </div>
        ) : (
          <div className="Item">
            <Link to="/user">
              <p>My Account</p>
            </Link>
          </div>
        )}
      </div>
      <div className="icon" onClick={() => setVisible(!visible)}>
        <i className="fas fa-bars" />
      </div>
    </div>
    //      <div className="Navbar">
    //      <div className="Navlogo">
    //          <img src ={Img1} alt="logo"></img>
    //      </div>
    //      <div id={visible ? "hidden":""} className= "Navdetail">
    //         <div><p>Home</p></div>
    //         <div><p>Home</p></div>
    //         <div><p>Home</p></div>
    //         <div><p>Home</p></div>
    //         <div><p>Home</p></div>
    //         <div><p>Home</p></div>
    //         <div><p>Home</p></div>
    //      </div>
    //      <div className="icon" onClick={()=>setVisible(!visible)}>
    //          <i className="fas fa-bars" />
    //      </div>

    //  </div>
  );
};
export default Nav;
