import React from "react";
import "./FeedBack.css";

const Contact = () => {
  return (
    <div className="contact">
      <div className="contact_polygon"></div>
      <div className="contact_rapper">
        <div className="contact_text">
          <h2>We welcome your questions</h2>
          <h4>Email:email@gmail.com</h4>
        </div>
        <div className="feedback_form">
          <h3>How was your Experience ?</h3>
          <p>Demo Line</p>
          <div className="feedback_form_rapper">
            <div className="ContactName">
              <input type="text" placeholder="Name" />
            </div>
            <div className="ContactEmail">
              <input type="text" placeholder="Email" />
            </div>
            <div className="ContactFeedback">
              <textarea maxLength="150" placeholder="FeedBack"></textarea>
            </div>
            <div className="Contact_button">
              <p>Submit</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
