import React from "react";
import "./PropertyCard.css";
// import Img from "../../img/hero-bg.jpg";
import { Link } from "react-router-dom";

const PropertyCard = ({ name, price, location, id, img, BHK }) => {
  console.log(img);
  return (
    <div className="col-3">
      <Link to={`/PropertyDetail/${id}`}>
        <div
          data-aos="fade-in"
          data-aos-delay="50"
          data-aos-duration="800"
          data-aos-easing="ease-in-out"
        >
          <div className="Card">
            <div className="Card_img">
              <img src={img} alt=""></img>
            </div>
            <div className="Card_detail">
              <h2>{name}</h2>
              <h4>{price}</h4>
              <p>{BHK} BHK</p>
              <p>{location}</p>
            </div>
          </div>
        </div>
      </Link>
    </div>
  );
};

export default PropertyCard;
