import React, { useEffect, useState } from "react";
import PropertyDetailCard from "./PropertyDetailCard";
import AdminFilterProperty from "./AdminFilterProperty";
import "./AdminPropertyView.css";
import ReactPaginate from "react-paginate";
import axios from "axios";

const AdminPropertyView = () => {
  const [data, setData] = useState([]);
  useEffect(() => {
    axios
      .get("/property/get", { withCredentials: true })
      .then(async (res) => {
        await setData(res.data.detailProperty);
        console.log(res.data.detailProperty);
        // setShow(true)
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  console.log(data);

  const deleteProperty = (id) => {
    axios
      .delete(`/property/delete/${id}`, {
        withCredentials: true,
      })
      .then(async (res) => {
        await setData(data.filter((item) => item._id !== id));
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  function searchProperty(name, location, state) {
    let query = "";
    if (name) {
      query = query + `name=${name}&`;
    }

    if (location) {
      query = query + `location=${location}&`;
    }

    if (state) {
      query = query + `reviewed=${state}&`;
    }

    if (query) {
      query = "?" + query;
      console.log(query);
    }
    axios
      .get(`/property/get${query}`, {
        withCredentials: true,
      })
      .then((res) => {
        console.log(res.data);
        setData(res.data.detailProperty);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <>
      {data ? (
        <div className="Admin_property_view">
          <AdminFilterProperty searchProperty={searchProperty} />
          <div className="Admin_property_view_row">
            {data.map((val) => (
              <PropertyDetailCard
                key={val._id}
                BHK={val.BHK}
                About={val.about}
                Disclaimer={val.disclaimer}
                Floor={val.floor}
                FullAddress={val.full_address}
                Highlight={val.highlight}
                LaunchDate={val.launch_date}
                Location={val.location}
                Name={val.name}
                Pincode={val.pin_code}
                Price={val.price}
                ProjectArea={val.project_area}
                ProjectType={val.project_type}
                StateOfProperty={val.state_of_property}
                id={val._id}
                deleteProperty={deleteProperty}
                reviewed={val.reviewed}
              />
            ))}
          </div>
          <ReactPaginate
            previousLabel={"←"}
            nextLabel={"Next →"}
            pageCount={10}
            // onPageChange={name}
            containerClassName={"pagination"}
            disabledClassName={"pagination_disabled"}
            activeClassName={"pagination_active"}
          />
        </div>
      ) : (
        <h2>Data Not Found</h2>
      )}
    </>
  );
};

export default AdminPropertyView;
