import React from "react";
import ContactImg from "../../img/undraw_contact_us.png";
import "./ContactUs.css";

const ContactUs = () => {
  return (
    <div className="Contact_us">
      <div className="Contact_us_rapper">
        <div className="Contact_us_details">
          <h1>LOGO</h1>
          <h2>Contact Us</h2>
          <p>Mobile</p>
          <p>Email</p>
          <p>Address</p>
        </div>
        <div className="Contact_us_svg">
          <img src={ContactImg} alt="display"></img>
        </div>
      </div>
    </div>
  );
};

export default ContactUs;
