import React from "react";
import "./About.css";
import { bounce } from "react-animations";
import Radium, { StyleRoot } from "radium";
import team from "../../img/about.jpg";

const About = () => {
  const styles = {
    bounce: {
      animation: "x 4s",
      animationName: Radium.keyframes(bounce, "bounce"),
    },
  };
  return (
    <div className="About">
      <StyleRoot>
        <h2 style={styles.bounce}>About</h2>
      </StyleRoot>
      <div
        data-aos="fade-right"
        data-aos-delay="200"
        data-aos-duration="600"
        data-aos-easing="ease-in-sine"
        className="About_rapper"
      >
        <div className="About_img">
          <img src={team} alt="About"></img>
        </div>
        <div className="About_detail">
          <span>Target Realty Service</span> is a leading residential and
          commercial property marketing company in Kolkata since 2012, rapidly
          growing with our knowledge, dedication and passion along with
          simplicity. We endeavour in making our clients satisfied with their
          choice by providing our best services, as we believe in building
          lifelong bonds of trust with them.
        </div>
      </div>
      <div
        data-aos="zoom-in-up"
        data-aos-delay="200"
        data-aos-duration="800"
        data-aos-easing="ease-in-out"
        className="About_rapper1"
      >
        <div className="About_detail1">
          <span>We</span> offer detailed information of every property which is
          easily understandable and provide large number of options to choose
          from, and no doubt, the best options. Our dedicated team members
          always give their 100% to help the clients find, what they are looking
          for and take on complete responsibility of their requirements. We
          constantly push ourselves to be our best, we focus on solutions and we
          arrive everyday inspired to make an impact through our hardwork.
        </div>
        <div className="About_img1">
          <img src={team} alt="About"></img>
        </div>
      </div>
    </div>
  );
};

export default About;
