import React, { useState } from "react";
import "./search.css";

const Searchbar = ({filterFun}) => {
  const [propertyType, setPropertyType] = useState('')
  const [bhk,setBHK] = useState('')
  const [price,setPrice] = useState('')
  const [location,setLocation] = useState('')
  return (
    <div data-aos="zoom-in" data-aos-delay="500" className="Search">
      <div className="Searchbar">
        <div className="three_filter">
          <div className="Search_detail">
            <i className="fas fa-home"></i>
            <select onChange={(event)=>setPropertyType(event.target.value)}>
              <option value="">Property Type</option>
              <option value="Flat">Flat</option>
              <option value="House/Villa">House/Villa</option>
              <option value="Plot">Plot</option>
            </select>
          </div>
          <div className="Search_detail2">
            <select onChange={(event)=>setBHK(event.target.value)}>
              <option value="">BHK</option>
              <option value="1">1 Bhk</option>
              <option value="2">2 Bhk</option>
              <option value="3">3 Bhk</option>
              <option value="4+">4+ Bhk</option>
            </select>
          </div>

          <div className="Search_detail3">
            <i className="fas fa-rupee-sign"></i>
            <select onChange={(event)=>setPrice(event.target.value)}>
              <option value="">Price</option>
              <option value="5">5-10 lakhs</option>
              <option value="10">10-20 lakhs</option>
              <option value="20">20-30 lakhs</option>
              <option value="30">30-40 lakhs</option>
              <option value="40">40+ lakhs</option>
            </select>
          </div>
        </div>
        <div className="Search_location">
          <i className="fas fa-map-marker-alt"></i>
          <input type="text" placeholder="Location" onChange={(event)=>setLocation(event.target.value)}/>
        </div>
        <div className="Search_button">
          <a href="#detailCard">
          <button onClick={()=>filterFun(propertyType,bhk,price,location)}>
            <i className="fas fa-search"></i>Search
          </button>
          </a>
        </div>
      </div>
    </div>
  );
};

export default Searchbar;
