import React from "react";
import "./Helpdesk.css";
import HelpdeskCard from "./HelpdeskCard";
import { Link } from "react-router-dom";

const Helpdesk = () => {
  return (
    <div className="help_desk">
      <div className="help_desk_rapper">
        <div className="help_desk_video">
          <h2>HOW WE CAN HELP YOU ?</h2>
          <p>
            Search or browse in depth articles and videos on everything on Docs
            jekyll theme, from basic theme setup to customisation and
            development
          </p>
          <div className="help_desk_video_card"></div>
        </div>
        <div className="help_desk_rapper1">
          <div className="help_desk_member">
            <h2>WE ARE HERE TO HELP</h2>
            <p>Our team is just an email away ready to answer your questions</p>
          </div>
          <div className="help_desk_card">
            <HelpdeskCard></HelpdeskCard>
          </div>
        </div>
      </div>
      <div className="ContactUs">
        <h2>Didn't find an answer?</h2>
        <p>
          Get in touch with us for details on setup and additional custom
          servers pricing
        </p>
        <Link to="/contactus">
          <button>Contact us</button>
        </Link>
      </div>
    </div>
  );
};

export default Helpdesk;
