import axios from "axios";
import React, { useEffect, useState } from "react";
import ReactPaginate from "react-paginate";
import images from "../../img/house.webp";
import Search from "./Search/Searchbar";
import { Carousel } from "react-responsive-carousel";

import PropertyCard from "./PropertyCard";
import "./HomePage.css";

const HomePage = () => {
  const [data, setData] = useState([]);
  const home = <i className="fas fa-home"></i>;

  const filterFun = (propertyType, bhk, price, location) => {
    console.log(propertyType, bhk, price, location);
    let query = "";
    if (propertyType) {
      query = query + `property_type=${propertyType}&`;
    }

    if (bhk) {
      if (bhk === "4+") {
        query = query + `BHK[gte]=4&`;
      } else {
        query = query + `BHK=${bhk}&`;
      }
    }

    if (location) {
      query = query + `location=${location}&`;
    }

    if (price) {
      if (price === "5") {
        query = query + `price[gte]=500000&price[lte]=1000000&`;
      } else if (price === "10") {
        query = query + `price[gte]=1000000&price[lte]=2000000&`;
      } else if (price === "20") {
        query = query + `price[gte]=2000000&price[lte]=3000000&`;
      } else if (price === "30") {
        query = query + `price[gte]=3000000&price[lte]=4000000&`;
      } else if (price === "40") {
        query = query + `price[gte]=4000000&`;
      }
    }

    if (query) {
      query = "?" + query;
      axios
        .get(`/publish/propertyView${query}`)
        .then((res) => {
          setData(res.data.allPropertyView);
          console.log(res.data.allPropertyView);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };

  useEffect(() => {
    axios
      .get("/publish/propertyView")
      .then((res) => {
        console.log(res);
        setData(res.data.allPropertyView);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  function name() {
    console.log("hello");
  }
  return (
    <>
      <div className="Content">
        <div className="Content_carousel">
          <Carousel
            showArrows={false}
            autoPlay
            interval="8000"
            infiniteLoop
            useKeyboardArrows={false}
            showIndicators={false}
            showStatus={false}
            showThumbs={false}
            transitionTime="800"
          >
            <div className="Img_slider">
              <img src={images} alt="bg" />
            </div>
            <div className="Img_slider">
              <img src={images} alt="image" />
            </div>
          </Carousel>
        </div>
        <div className="Bg_text">
          <div className="Maintext">
            <div className="text">
              <h2>Home is not a Place...</h2>
              <h2>it's a feeling.</h2>
            </div>
            <Search filterFun={filterFun} />
            <p>
              Get your Dream Shade
              <span className="Home0">{home}</span>
              <span className="Home1">{home}</span>
              <span className="Home2">{home}</span>
              <span className="Home3">{home}</span>
            </p>
          </div>
        </div>
      </div>
      <div id="detailCard" className="Cardbg">
        <h2 className="card_title">Projects</h2>
        <div className="row">
          {data.map((el) =>
            el.propertyId ? (
              <PropertyCard
                key={el._id}
                id={el._id}
                img={el.propertyId.img}
                name={el.propertyId.name}
                price={el.propertyId.price}
                location={el.propertyId.location}
                BHK={el.propertyId.BHK}
              />
            ) : null
          )}
        </div>
        <ReactPaginate
          previousLabel={"←"}
          nextLabel={"Next →"}
          pageCount={10}
          onPageChange={name}
          containerClassName={"pagination"}
          disabledClassName={"pagination_disabled"}
          activeClassName={"pagination_active"}
        />
      </div>
    </>
  );
};

export default HomePage;
