import React from "react";
import "./TeamCard.css";
import Image from "../../img/hero-bg.jpg";

const TeamCard = () => {
  return (
    <div data-aos="zoom-in" data-aos-delay="500" className="Team_card_col">
      <div className="Team_card_rapper">
        <div className="Team_card_img">
          <img src={Image} alt="logo"></img>
        </div>
        <div className="Team_card_info">
          <h2>Name</h2>
          <p>Designation</p>
        </div>
        <div className="Team_card_social">
          <a href="https://www.youtube.com/" target="_blank" rel="noreferrer">
            <i className="fab fa-facebook"></i>
          </a>
          <a href="https://www.youtube.com/" target="_blank" rel="noreferrer">
            <i className="fab fa-instagram"></i>
          </a>
          <a href="https://www.youtube.com/" target="_blank" rel="noreferrer">
            <i className="fab fa-twitter"></i>
          </a>
          <a href="https://www.youtube.com/" target="_blank" rel="noreferrer">
            <i className="fab fa-youtube"></i>
          </a>
        </div>
      </div>
    </div>
  );
};

export default TeamCard;
