import React, { useState } from "react";
import "./AdminFilter.css";

const AdminFilterUser = ({ searchUser }) => {
  const [role, setRole] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");

  return (
    <div className="admin_Filter">
      <div className="admin_Filter_rapper">
        <div className="admin_Filter_rapper1">
          <div className="admin_FilterProperty_Role">
            <select name="" onChange={(event) => setRole(event.target.value)}>
              <option value="">Role</option>
              <option value="admin">Admin </option>
              <option value="dealer">Dealer</option>
              <option value="user">User</option>
            </select>
          </div>
          <div className="admin_Filter_Name">
            <input
              type="text"
              placeholder="Name"
              onChange={(event) => setName(event.target.value)}
            />
          </div>
        </div>
        <div className="admin_Filter_rapper2">
          <div className="admin_Filter_email">
            <input
              type="text"
              placeholder="Email"
              onChange={(event) => setEmail(event.target.value)}
            />
          </div>
          <div className="admin_Filter_phone">
            <input
              type="number"
              placeholder="Phone"
              onChange={(event) => setEmail(event.target.value)}
            />
          </div>
        </div>
        <div className="admin_Filter_Button">
          <button onClick={() => searchUser(name, email, role)}>Search</button>
        </div>
      </div>
    </div>
  );
};

export default AdminFilterUser;
