import React, { useState, useEffect } from "react";
import "./PropertyDetailCard.css";

const PropertyDetailCard = ({
  Name,
  Location,
  BHK,
  Price,
  StateOfProperty,
  About,
  LaunchDate,
  ProjectType,
  ProjectArea,
  Pincode,
  FullAddress,
  Highlight,
  Disclaimer,
  Floor,
  id,
  deleteProperty,
  reviewed,
}) => {
  const [icon, setIcon] = useState("");
  const [className, setClassName] = useState("");
  useEffect(() => {
    if (reviewed === "Approved") {
      setClassName("approve");
      setIcon("far fa-check-square");
    }
    if (reviewed === "Rejected") {
      setClassName("reject");
      setIcon("far fa-window-close");
    }
    if (reviewed === "Sold Out") {
      setClassName("sold_Out");
      setIcon("fas fa-hand-holding-usd");
    }
  });
  return (
    <div className="column-2">
      <div className="Property_detail_card">
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <h4>Name: </h4>
            <hr></hr>
          </div>
          <div className="Review_details">
            <h4>{Name}</h4>
            <hr />
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Location: </p>
          </div>
          <div className="Review_details">
            <p>{Location}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>BHK: </p>
          </div>
          <div className="Review_details">
            <p>{BHK}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Price: </p>
          </div>
          <div className="Review_details">
            <p>{Price}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>State of Property: </p>
          </div>
          <div className="Review_details">
            <p>{StateOfProperty}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>About: </p>
          </div>
          <div className="Review_details">
            <p>{About}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Launch Date: </p>
          </div>
          <div className="Review_details">
            <p>{LaunchDate}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Project Type: </p>
          </div>
          <div className="Review_details">
            <p>{ProjectType}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            {/* <p>Property Type</p> */}
          </div>
          <div className="Review_details">{/* <p>{ProjectType}</p> */}</div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Project Area: </p>
          </div>
          <div className="Review_details">
            <p>{ProjectArea}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Pincode: </p>
          </div>
          <div className="Review_details">
            <p>{Pincode}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Full Address: </p>
          </div>
          <div className="Review_details">
            <p>{FullAddress}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Full Address: </p>
          </div>
          <div className="Review_details">
            <p>{FullAddress}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Highlights: </p>
          </div>
          <div className="Review_details">
            <p>{Highlight}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Disclaimer: </p>
          </div>
          <div className="Review_details">
            <p>{Disclaimer}</p>
          </div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            {/* <p>Location Advance</p> */}
          </div>
          <div className="Review_details">{/* <p>Location Advance</p> */}</div>
        </div>
        <div className="Property_detail_card_rapper">
          <div className="Property_detail_card_label">
            <p>Floor: </p>
          </div>
          <div className="Review_details">
            <p>{Floor}</p>
          </div>
        </div>

        {/* <button type="button"   onClick={()=>setVisible(true)}>Map</button> */}

        <p className={className}>
          <i className={icon}></i> {reviewed}
        </p>

        <button onClick={() => deleteProperty(id)}>Delete</button>
      </div>
    </div>
  );
};

export default PropertyDetailCard;
