import React, { useState, useEffect } from "react";
import "./UserCard.css";

const UserCard = ({
  Name,
  Location,
  BHK,
  Price,
  StateOfProperty,
  About,
  LaunchDate,
  ProjectType,
  ProjectArea,
  Pincode,
  FullAddress,
  Highlight,
  Disclaimer,
  Floor,
  reviewed,
}) => {
  const [className, setClassName] = useState("");
  const [icon, setIcon] = useState("");
  useEffect(() => {
    if (reviewed === "Approved") {
      setClassName("approve");
      setIcon("far fa-check-square");
    }
    if (reviewed === "Rejected") {
      setClassName("reject");
      setIcon("far fa-window-close");
    }
    if (reviewed === "Sold Out") {
      setClassName("sold_Out");
      setIcon("fas fa-hand-holding-usd");
    }
  }, []);
  return (
    <div className="User_column2">
      <div className="User_card">
        <div className="User_card_rapper">
          <div className="User_card_label">
            <h4>Name</h4>
          </div>
          <div className="User_card_details">
            <h4>{Name}</h4>
          </div>
        </div>
        <hr></hr>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Location</p>
          </div>
          <div className="User_card_details">
            <p>{Location}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>BHK</p>
          </div>
          <div className="User_card_details">
            <p>{BHK}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Price</p>
          </div>
          <div className="User_card_details">
            <p>{Price}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>State of Property</p>
          </div>
          <div className="User_card_details">
            <p>{StateOfProperty}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>About</p>
          </div>
          <div className="User_card_details">
            <p>{About}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Launch Date</p>
          </div>
          <div className="User_card_details">
            <p>{LaunchDate}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Project Type</p>
          </div>
          <div className="User_card_details">
            <p>{ProjectType}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Project Area</p>
          </div>
          <div className="User_card_details">
            <p>{ProjectArea}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Pincode</p>
          </div>
          <div className="User_card_details">
            <p>{Pincode}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Full Address</p>
          </div>
          <div className="User_card_details">
            <p>{FullAddress}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Highlights</p>
          </div>
          <div className="User_card_details">
            <p>{Highlight}</p>
          </div>
        </div>
        <div className="User_card_rapper">
          <div className="User_card_label">
            <p>Floor</p>
          </div>
          <div className="User_card_details">
            <p>{Floor}</p>
          </div>
        </div>
        <hr></hr>
        <p className={className}>
          <i className={icon}></i> {reviewed}
        </p>
      </div>
    </div>
  );
};

export default UserCard;
