import React from "react";
import { useHistory } from "react-router-dom";

import { Link } from "react-router-dom";
import "./Signup.css";
import axios from "axios";
import { useForm } from "react-hook-form";

const Signup = () => {
  const history = useHistory();
  const { register, handleSubmit } = useForm();
  const onSubmit = (data) => {
    axios
      .post("/admin/signup", data)
      .then((data) => {
        console.log(data);
        history.push("/Login");
      })
      .catch((err) => {
        console.log(err);
      });
  };
  return (
    <div className="Signup">
      <div className="Signup_detail ">
        <div className="Signup_img"></div>

        <div className="Signup_info">
          <h2>Sign up</h2>
          <p>Welcomeback!Log in to your account to view today's clients:</p>

          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="txt_field">
              <i className="far fa-user"></i>
              <input
                type="text"
                {...register("name")}
                placeholder="Name"
              ></input>
            </div>

            <div className="txt_field">
              <i className="far fa-envelope"></i>
              <input type="text" {...register("email")} placeholder="Email" />
            </div>

            <div className="txt_field">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                {...register("password")}
                placeholder="Password"
              />
            </div>

            <div className="txt_field">
              <i className="fas fa-lock"></i>
              <input
                type="password"
                {...register("confirmPassword")}
                placeholder="Confirm Password"
              />
            </div>

            <div className="txt_field">
              <i className="fas fa-phone-alt"></i>
              <input
                type="text"
                {...register("phone")}
                placeholder="Mobile/Lan No."
              />
            </div>

            <div className="txt_field">
              <i className="far fa-id-card"></i>
              <input
                type="text"
                {...register("address")}
                placeholder="Address"
              />
            </div>

            <div className="sign_button">
              <input type="submit" value="Sign up" />
            </div>
          </form>
          <div className="signup_link">
            All ready a member? <Link to="/Login">Login</Link>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Signup;
