import axios from "axios";
import React, { useEffect, useState } from "react";
import "./User.css";
import UserCard from "./UserCard";

const User = () => {
  const [data, setData] = useState([]);
  useEffect(() => {
    axios
      .get("/property/userallproperty", {
        withCredentials: true,
      })
      .then(async (res) => {
        await setData(res.data.userProperty);
        console.log(res.data.userProperty);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  console.log();
  return (
    <div className="user">
      {data.map((val) => (
        <UserCard
          key={val._id}
          BHK={val.BHK}
          About={val.about}
          Disclaimer={val.disclaimer}
          Floor={val.floor}
          FullAddress={val.full_address}
          Highlight={val.highlight}
          LaunchDate={val.launch_date}
          Location={val.location}
          Name={val.name}
          Pincode={val.pin_code}
          Price={val.price}
          ProjectArea={val.project_area}
          ProjectType={val.project_type}
          StateOfProperty={val.state_of_property}
          reviewed={val.reviewed}
        />
      ))}
    </div>
  );
};

export default User;
