import React, { useState, useEffect } from "react";
import ReactMapGL, { Marker } from "react-map-gl";
import mapboxgl from 'mapbox-gl/dist/mapbox-gl';
import MapboxWorker from 'mapbox-gl/dist/mapbox-gl-csp-worker';
import "./PropertyDetailMap.css";

const PropertyDetailMap = ({ latitude, longitude }) => {
  mapboxgl.workerClass = MapboxWorker;
  const [viewport, setViewport] = useState({
    latitude: 22.9868,
    longitude: 87.855,
    zoom: 7,
    height: "450px",
    width: "100%",
    pitch: 0,
  });
  const [lat, setLat] = useState(0);
  const [long, setLong] = useState(0);
  useEffect(async () => {
    await setLat(latitude);
    await setLong(longitude);
  }, []);

  return (
    <div className="Property_Detail_Mapbg">
      <div className="Property_Detail_Map">
        <ReactMapGL
          mapboxApiAccessToken={
            "pk.eyJ1Ijoid2VibG9vcCIsImEiOiJja21pdXBrMWQwbDBtMndzMjZnZ3FicTIxIn0.sTsSShxZ2LqjqUWb_akSyw"
          }
          mapStyle="mapbox://styles/mapbox/streets-v11"
          {...viewport}
          onViewportChange={(newView) => setViewport(newView)}
        >
          {/* {lat === 0 && long === 0 ? null : (
            <Marker latitude={lat} longitude={long}>
              <i className=" Marker fas fa-map-pin"></i>
            </Marker>
          )} */}
          <Marker latitude={lat} longitude={long}>
            <i className=" Marker fas fa-map-pin"></i>
          </Marker>
        </ReactMapGL>
      </div>
    </div>
  );
};

export default PropertyDetailMap;
