import React from "react";
import "./AdminDrawer.css";
import { Link } from "react-router-dom";

const AdminDrawer = ({ closeDrawer }) => {
  return (
    <div className="Admin_drawer">
      <div className="Admin_drawer_icon">
        <i onClick={() => closeDrawer()} className="fas fa-times"></i>
      </div>
      <Link to="/Admin/UserDetails">
        <p>User Details</p>
      </Link>
      <Link to="/Admin/PropertyDetails">
        <p>Property Details</p>
      </Link>
    </div>
  );
};

export default AdminDrawer;
