import React, { useEffect, useState } from "react";
// import Img from "../../img/hero-bg.jpg";
import "./Property_detail.css";
import PropertyDetailMap from "./PropertyDetailMap";
import axios from "axios";
import { useParams } from "react-router";

const PropertyDetail = () => {
  const [data, setData] = useState([]);
  const { itemId } = useParams();
  useEffect(() => {
    axios
      .get(`/publish/singleProperty/${itemId}`)
      .then((res) => {
        console.log(res.data.propertyData.propertyId);
        setData(res.data.propertyData.propertyId);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [itemId]);
  console.log(data.name);
  console.log(data.latitude);
  console.log(data.longitude);
  return (
    <>
      <div className="Property_img">
        <img src={data.img} alt="img" />
      </div>
      <div className="Property_details">
        <div className="Property_description">
          <div className="Property_name">
            <p>{data.name}</p>
            <p>{data.location}</p>
          </div>

          <div className="Property_review">
            <p>Write a review</p>
          </div>
        </div>
        <hr></hr>
        <div className="Property_value">
          <div className="Property_price">
            <h3>₹ {data.price}</h3>
            <p>{data.BHK} BHK Flat</p>
          </div>
          <div className="Property_contact">
            <p>Contact Builder</p>
          </div>
        </div>
        <hr></hr>
        <div className="Property_about">
          <h2>About {data.name}</h2>
          <p>
            {data.about}
            <span>read more</span>
          </p>
        </div>

        <div className="Property_alldetail">
          <div className="Property_alldetail_rapper1">
            <div className="Property_Status">
              <p>Status</p>
              <h6>{data.state_of_property}</h6>
              <p>Total Towers</p>
              <h6>6</h6>
              <p>Occupancy Certificate</p>
              <h6>No</h6>
            </div>
            <div className="Property_launch">
              <p>Launch Date</p>
              <h6>{data.launch_date}</h6>
              <p>Project Type</p>
              <h6>{data.project_type}</h6>
              <p>Full Address</p>
              <h6>{data.full_address}</h6>
            </div>
          </div>
          <div className="Property_alldetail_rapper2">
            <div className="Property_prossession">
              <p>Possession by</p>
              <h6>Under</h6>
              <p>Property Type</p>
              <h6>{data.property_type}</h6>
              <p>Pincode</p>
              <h6>{data.pin_code}</h6>
            </div>
            <div className=" Property_Units">
              <p>Project Area</p>
              <h6>{data.project_area}</h6>
              <p>Total Units</p>
              <h6>Under</h6>
              <p>Commencement Certificate</p>
              <h6>6</h6>
            </div>
          </div>
        </div>
        <div className="Property_disclaimer">
          <h2>Disclaimer</h2>
          <p>{data.disclaimer}</p>
        </div>
        <div className="Property_highlights">
          <h2>Highlight</h2>
          <p>
            <i className="far fa-check-circle"></i> {data.highlight}
          </p>
        </div>
        <hr></hr>
        <div className="Property_Amenities">
          <h2>Amenities</h2>
          <div></div>
        </div>

        <div className="Property_Specifications">
          <h2>Specifications of {data.name}</h2>
          <p>
            Flooring: Balcony Vitrified Tiles Kitchen Imported Vitrified Tiles
            Living/Dining Imported Vitrified Tiles Master Bedroom Wooden
            Flooring Other Bedroom Vitrified Tiles Toilet Modern washroom
          </p>
          <p>
            Fitting: Internal Door Air conditioning Electrical Telephone and T.V
            connection Kitchen International style kitchen with hob and chimney
            ,water purifire
          </p>
          <p>Walls: Exterior : wallputy/Gypsum finish for wall and ceiling</p>
        </div>
        <div className="Property_Locality">
          <h2>About Locality</h2>
          <h4>{data.location}</h4>
          <p>
            Rajarhat is a prominent neighbourhood that falls under the
            jurisdiction of Bidhan Nagar Municipal Corporation of North 24
            Parganas district. It is a p...{" "}
          </p>
          <p>
            <i className="far fa-check-circle"></i> Easy access to major IT
            parks situated in Salt Lake and New Town
          </p>
          <p>
            <i className="far fa-check-circle"></i> Closely surrounded with
            prominent areas which offers easy access to developed social infra
          </p>
        </div>
        <div className="Property_trend">
          <h2>Price Trends</h2>
          <p>
            The Average Price of property in Rajotto is estimated to be ₹3679
            for the 'Jan21' month
          </p>
        </div>
      </div>
      {!data.latitude || !data.longitude ? null : (
        <PropertyDetailMap
          latitude={data.latitude}
          longitude={data.longitude}
        />
      )}
    </>
  );
};

export default PropertyDetail;
