import React, { useState } from "react";
import { Route } from "react-router-dom";
import "./Admin.css";
import AdminDrawer from "./AdminDrawer/AdminDrawer";
import AdminPropertyView from "./PropertyDetails/AdminPropertyView";
import AdminUserView from "./UserDetails/AdminUserView";

const Admin = () => {
  const [visible, setVisible] = useState(false);
  function closeDrawer() {
    setVisible(false);
  }
  return (
    <div className="admin">
      {visible ? <AdminDrawer closeDrawer={closeDrawer} /> : null}
      <i
        onClick={() => setVisible(true)}
        className="fas fa-bars Admin_Drawer_bar"
      ></i>
      <Route path="/Admin/UserDetails" component={AdminUserView}></Route>
      <Route
        path="/Admin/PropertyDetails"
        component={AdminPropertyView}
      ></Route>
    </div>
  );
};

export default Admin;
