import React from 'react';
import './login.css';
// import {useCookies} from "react-cookie";
import {Link} from 'react-router-dom';
import {useForm} from 'react-hook-form';
import {useHistory} from 'react-router-dom';
import axios from 'axios';

const Login = () => {

    const history =useHistory()
    const {register, handleSubmit} =useForm()
    // const [cookies, setCookie] = useCookies(['cookie-name'])
    const onSubmit = data =>{
        axios.post("/admin/login", data, {withCredentials:true})
        .then(async (res) =>{
            console.log(res)
            await localStorage.setItem("userName", res.data.userInfo.name);
            await localStorage.setItem("userRole", res.data.userInfo.role);
            // await setCookie("isAuth","yes",{path: "/", expires: new Date(Date.now() + 24 * 60 * 60 * 1000 * 7)})
            history.push("/")
        })
        .catch((err) =>{console.log(err)})
    }
    return (
        
        <div className="Login">
            <div className="login_detail ">
                <div className="Log_img">
                
                </div>
                <div className="log_info">
                <h2>Login</h2>
                <p>Welcomeback!Log in to your account to view today's clients:</p>
                <form onSubmit={handleSubmit(onSubmit)}>
                <div className="txt_field">
                    <i className="far fa-envelope"></i>
                    <input type="text" {...register("email")} placeholder="Email"></input>
                 </div>
                <div className="txt_field">
                    <i className="fas fa-lock"></i>
                    <input type="password" {...register("password")} placeholder="Password"/>
                </div>
                <div className="forgot_password">Forgot Password?</div>
                    <div className="log_button">
                        <input type="submit" value="Login" />
                    </div>
                    </form>
                <div className="signup_link">
                   Not a member? <Link to="/Signup">Signup</Link>
                </div>
                </div>
            </div>

        </div>
      
    )
}

export default Login;
