import React, { useState } from "react";
import ReactMapGL, { Marker } from "react-map-gl";
import mapboxgl from 'mapbox-gl/dist/mapbox-gl';
import MapboxWorker from 'mapbox-gl/dist/mapbox-gl-csp-worker';
import "./Map.css";

const Map = ({ setCoordinate }) => {
  mapboxgl.workerClass = MapboxWorker;
  const [latitude, setLatitude] = useState(22.5726);
  const [longitude, setLogitude] = useState(88.3639);
  const [viewport, setViewport] = useState({
    latitude: 22.9868,
    longitude: 87.855,
    zoom: 7,
    height: "500px",
    width: "750px",
    pitch: 0,
  });
  function setLngLat(event) {
    setLatitude(event.lngLat[1]);
    setLogitude(event.lngLat[0]);
    setCoordinate(event.lngLat[1], event.lngLat[0]);
  }
  return (
    <div className="Data_map">
      <ReactMapGL
        width="100%"
        mapboxApiAccessToken={
          "pk.eyJ1Ijoid2VibG9vcCIsImEiOiJja21pdXBrMWQwbDBtMndzMjZnZ3FicTIxIn0.sTsSShxZ2LqjqUWb_akSyw"
        }
        mapStyle="mapbox://styles/mapbox/streets-v11"
        {...viewport}
        onViewportChange={(newView) => setViewport(newView)}
        onClick={(event) => setLngLat(event)}
      >
        <Marker latitude={latitude} longitude={longitude}>
          <i className=" Marker fas fa-map-pin"></i>
        </Marker>
      </ReactMapGL>
    </div>
  );
};

export default Map;
