import React, { useState } from "react";
import "./AdminFilter.css";

const AdminFilter = ({ searchProperty }) => {
  const [state, setState] = useState("");
  const [location, setLocation] = useState("");
  const [name, setName] = useState("");

  return (
    <div className="admin_Filter">
      <div className="admin_property_Filter_rapper">
        <div className="Admin_property_Filter_rapper1">
          <div className="admin_property_Filter_detail1">
            <select name="" onChange={(event) => setState(event.target.value)}>
              <option value="">State</option>
              <option value="Rejected">Rejected </option>
              <option value="Approved">Approved</option>
              <option value="Sold out">Sold out</option>
              <option value="Not Set">Not Set</option>
            </select>
          </div>
          <div className="admin_property_Filter_location">
            <input
              type="text"
              placeholder="Location"
              onChange={(event) => {
                setLocation(event.target.value);
              }}
            />
          </div>
        </div>
        <div className="Admin_property_Filter_rapper2">
          <div className="admin_property_Filter_Name">
            <input
              type="text"
              placeholder="Name"
              onChange={(event) => {
                setName(event.target.value);
              }}
            />
          </div>
        </div>
        <div className="admin_Filter_button">
          <button onClick={() => searchProperty(name, location, state)}>
            Search
          </button>
        </div>
      </div>
    </div>
  );
};

export default AdminFilter;
