import React, { useEffect, useState } from "react";
import UserDetailCard from "./UserDetailCard";
import axios from "axios";
import AdminFilterUser from "./AdminFilterUser";

const AdminUserView = () => {
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get("/admin/getUser", { withCredentials: true })
      .then(async (res) => {
        await setData(res.data.getUser);
        console.log(res.data);
      })

      .catch((err) => {
        console.log(err);
      });
  }, []);

  console.log(data);
  const deleteUser = (id) => {
    axios
      .delete(`/admin/deleteUser/${id}`, {
        withCredentials: true,
      })
      .then(async (res) => {
        await setData(data.filter((item) => item._id !== id));
        console.log(res);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  function searchUser(name, email, role) {
    let query = "";
    if (name) {
      query = query + `name=${name}&`;
    }

    if (email) {
      query = query + `email=${email}&`;
    }

    if (role) {
      query = query + `role=${role}&`;
    }

    if (query) {
      query = "?" + query;
    }
    axios
      .get(`/admin/getUser${query}`, {
        withCredentials: true,
      })
      .then((res) => {
        console.log(res.data);
        setData(res.data.getUser);
      })
      .catch((err) => {
        console.log(err);
      });
  }

  return (
    <>
      {data ? (
        <div className="Admin_user_view">
          <AdminFilterUser searchUser={searchUser} />
          <div className="Admin_row">
            {data.map((item) => (
              <UserDetailCard
                key={item._id}
                name={item.name}
                email={item.email}
                phone={item.phone}
                role={item.role}
                id={item._id}
                deleteUser={deleteUser}
              />
            ))}
          </div>
        </div>
      ) : (
        <h2>Data Not Found</h2>
      )}
    </>
  );
};

export default AdminUserView;
