import React from 'react';
import MainFile from './MainFile';
import AOS from 'aos';
import {Route,Switch,Redirect} from 'react-router-dom';
import About from './components/About/About';
import Login from './components/Login/Login';
import Nav from './components/Navbar/Nav';
import Signup from './components/Signup/Signup';
import Property_detail from './components/Property_detail/Property_detail';
import Data_Entry from './components/Data_Entry/Data_Entry';
import Admin from './components/Admin/Admin';
import FeedBack from './components/FeedBack/FeedBack';
import User from './components/User/User';
import Team from './components/Team/Team';
import Footer from './components/Footer/Footer';
import Helpdesk from './components/Help/Helpdesk';
import ContactUs from './components/Contact/ContactUs';



function App() {
  AOS.init();
  return (
    <>
    <Nav/>
     <Switch>
            <Route exact path="/" component={MainFile}></Route>     
           <Route  path="/about" component={About}></Route>
           <Route  path="/Login" component={Login}></Route>
           <Route path="/Signup" component={Signup}></Route>
           <Route path="/PropertyDetail/:itemId" component={Property_detail}></Route>
           <Route path="/Data_Entry" component={Data_Entry}></Route>
                <Route path="/Admin" component={Admin}>
                    
                </Route>
           <Route path="/Team" component={Team}></Route>     
           <Route path="/feedback" component={FeedBack}></Route>
           <Route path="/helpdesk" component={Helpdesk}></Route>
           <Route path="/contactus" component={ContactUs}></Route>
           <Route path="/User" component={User}></Route>
           <Redirect to="/" />
           </Switch>
           <Footer />
   </>
  );
}

export default App;
